import { createSelector } from 'reselect';
import { formatNumberToLocale } from '../../helpers/numberFormatter';

export const loyaltyTokenSelector = createSelector(
  [state => state.loyaltyFee.loyaltyFees, state => state.position.positions],
  (loyaltyFees, positions) => {
    const loyaltyOMSProducts = [];
    const loyaltyOMSEnabled = true;

    positions = Object.keys(positions).map(p => positions[p]);

    loyaltyFees.forEach(token => {
      if (token.IsEnabled) {
        const product = positions.find(x => {
          return x.ProductId === token.LoyaltyProductId;
        });

        if (product) {
          const discount = token.LoyaltyDiscount * 100;

          const decimals =
            Math.floor(discount) !== discount
              ? discount.toString().split('.')[1].length || 0
              : 0;

          product.LoyaltyDiscount = formatNumberToLocale(discount, decimals);
          loyaltyOMSProducts.push(product);
        }
      }
    });

    return { loyaltyOMSEnabled, loyaltyOMSProducts };
  }
);

export const currentAccountloyaltyTokenSelector = createSelector(
  [state => state.user.accounts, state => state.user.selectedAccountId],
  (accounts, selectedAccountId) =>
    accounts.find(account => selectedAccountId === account.AccountId)
);



