import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';

import LoyaltyTokenComponent from './LoyaltyTokenComponent';
import { getOperatorLoyaltyFeeConfigs } from '../../redux/actions/loyaltyFeeActions';
import { updateAccount } from '../../redux/actions/userActions';
import {
  loyaltyTokenSelector,
  currentAccountloyaltyTokenSelector
} from '../../redux/selectors/loyaltyTokenSelectors';

const mapStateToProps = state => {
  const { loyaltyOMSEnabled, loyaltyOMSProducts } = loyaltyTokenSelector(state);
  const currentAccount = currentAccountloyaltyTokenSelector(state);
  return { loyaltyOMSEnabled, loyaltyOMSProducts, currentAccount };
};

const mapDispatchToProps = {
  getOperatorLoyaltyFeeConfigs,
  updateAccount
};

const LoyaltyTokenContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(LoyaltyTokenComponent);

export default reduxForm({
  form: 'loyaltyTokenForm',
  onSubmit: () => {}
})(LoyaltyTokenContainer);



