import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APRadio from '../common/APRadio/APRadio';
import './LoyaltyToken.css';

const loyaltyTokenClasses = getBEMClasses('loyalty-token');

export default class LoyaltyTokenComponent extends React.Component {
  componentWillMount() {
    this.props.getOperatorLoyaltyFeeConfigs();
  }

  loyaltyDisabled = (loyaltyOMSEnabled, product) => {
    return !loyaltyOMSEnabled || product.Amount <= 0;
  };

  getLoyaltyLabel = (loyaltyDisabled, ProductSymbol, LoyaltyDiscount) => {
    const disabledText = this.context.t(
      `You must have a balance to use ${ProductSymbol} to pay for fees`
    );
    const enabledText = this.context.t('Use {ProductSymbol} to pay for fees', {
      ProductSymbol
    });
    const appendText = this.context.t('({LoyaltyDiscount}% discount )', {
      LoyaltyDiscount
    });

    return (loyaltyDisabled ? disabledText : enabledText) + ` ${appendText}`;
  };

  render() {
    const {
      loyaltyOMSEnabled,
      loyaltyOMSProducts,
      currentAccount,
      updateAccount
    } = this.props;
    const loyaltyProductId =
      currentAccount && currentAccount.LoyaltyEnabled
        ? currentAccount.LoyaltyProductId
        : 0;

    // Handle multiple loyalty products
    let displayProducts = '';
    if (loyaltyOMSProducts.length > 1) {
      const tempArray = loyaltyOMSProducts.slice().map(i => i.ProductSymbol);
      const tempLast = tempArray.pop();
      displayProducts = this.context.t('{products} or {lastProduct}', {
        products: tempArray.join(', '),
        lastProduct: tempLast
      });
    } else if (loyaltyOMSProducts.length === 1) {
      displayProducts = loyaltyOMSProducts[0].ProductSymbol;
    }

    return (
      <div className={loyaltyTokenClasses()}>
        <h1 className={loyaltyTokenClasses('header')}>
          {this.context.t('Trading Fees')}
        </h1>
        <p className={loyaltyTokenClasses('description')}>
          {displayProducts
            ? this.context.t(
                'Using {displayProducts} for transaction fees applies a discount to your trading fee. If you hold {displayProducts} in your account, you have the option of having your trading fees automatically subtracted from your {displayProducts} balance.',
                { displayProducts }
              )
            : this.context.t(
                'Loyalty token has not been set up on this exchange.'
              )}
        </p>

        {loyaltyOMSEnabled && (
          <div className="row loyalty">
            <div className="col-xs-12">
              {loyaltyOMSProducts.map((product, index) => {
                const disabled = this.loyaltyDisabled(
                  loyaltyOMSEnabled,
                  product
                );
                return (
                  <APRadio
                    key={product.ProductId}
                    id={`loyaltyToken${product.ProductId}`}
                    name="loyaltyToken"
                    value={index}
                    label={this.getLoyaltyLabel(
                      disabled,
                      product.ProductSymbol,
                      product.LoyaltyDiscount
                    )}
                    checked={loyaltyProductId === product.ProductId}
                    onChange={() => updateAccount(product)}
                    disabled={disabled}
                  />
                );
              })}
              <APRadio
                id="loyaltyToken"
                name="loyaltyToken"
                value={-1}
                label={this.context.t('Use default fees')}
                checked={loyaltyProductId === 0}
                onChange={() => updateAccount()}
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}

LoyaltyTokenComponent.defaultProps = {
  loyaltyOMSProducts: [],
  loyaltyOMSEnabled: false
};

LoyaltyTokenComponent.propTypes = {
  loyaltyOMSEnabled: PropTypes.bool,
  loyaltyOMSProducts: PropTypes.array
};

LoyaltyTokenComponent.contextTypes = {
  t: PropTypes.func.isRequired
};



